import { globalRoutingContext } from "../App";
import React from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import { TrackingSession } from '../Contexts/TrackingSessionProvider';

const useStyles = makeStyles((theme) => ({
  paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
  },
}));

const HomeCpt = () => {
  const classes = useStyles();
  return (
    <globalRoutingContext.Consumer>
    {({ 
            handleRoute
              }) => ( 
    <Container component="main" maxWidth="xs">      
    <CssBaseline />       
    <div className={classes.paper}>    
    <img className='authImg' src="data:image/png;base64,"/>
                <br/>
                <br/>
                <br/>                
                <Typography component="h1" variant="h5">
                    Activity tracking Extension
                </Typography>
                
                <TrackingSession>
                {({             
              }) => ( 
                <div>
                <br></br>
                <br></br>                
                    <Button
                    variant="outlined"
                    fullWidth
                    autoFocus
                    onClick={() => {
                      
                      handleRoute('TrackingIP')}
                      
                      }>
                      Start
                    </Button>                    
                    </div>
                  )}
                  
                  </TrackingSession>
                
                    <br>
                    </br>
                    <br></br>
                             
    </div>
    </Container>
    )}
    </globalRoutingContext.Consumer>
  );

};

export default HomeCpt;